@import url(https://cdn.syncfusion.com/ej2/material.css);
*{
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.nav_main {
    background-color: rebeccapurple;
    height: 100%;
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    padding: 10px;
    color: whitesmoke;
}
.nav_body {
    padding: 10px;
}
.nav_links{
    position: absolute;
    right: 0;
    display: -webkit-flex;
    display: flex;
}
.nav_link1, .nav_link2{
    margin-right: 50px;
}
.nav_link3 {
    margin-right: 20px;
}
.nav_link_text {
    text-decoration: none;
    color: white;
}
.nav_title_text {
    cursor: not-allowed;
}
.events_main {
    background-color: #f7f4f4;
    height: 100%;
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    padding: 50px;
    color: black;
    border-width:2px;
    border-style:solid;
    border-right: none;
    border-top: none;
    border-left: none;
    border-bottom-color:lightgray;
}
.events_body {
  font-size: 40px;
}
.events_buttons {
    display: -webkit-flex;
    display: flex;
}
.events_button {
    margin-right: 10%;
}
.events_create {
    position: absolute;
    right: 0;
}
.create_button {
    margin-right: 100px;
}
.calendar_main {
    display: -webkit-flex;
    display: flex;
    padding: 50px;

}
.calendar_title {
    margin-left: 100px;
    width: 50%;
}
